.page-body {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	width: 192px;
	width: 12rem;
	margin: 0 auto;
	padding: 0 8px;
	padding: 0 .5rem;
  font-size: 3.84px;
  font-size: .24rem;
  color: #333;
  background: #fff;
}

  .page-body-sidebar {
  	width: 56px;
  	width: 3.5rem;
  	padding: 32px 0 0;
  	padding: 2rem 0 0;
  	-webkit-box-shadow: .03rem 0 .01rem #999;
  	        box-shadow: .03rem 0 .01rem #999;
  }

  .page-body-main {
  	-webkit-box-flex: 1;
  	    -ms-flex: 1 1;
  	        flex: 1 1;
  	padding: 0 0 0 8px;
  	padding: 0 0 0 .5rem;
  	border-left: 0.16px solid;
  	border-left: .01rem solid;
  }

  .page-body-title {
  	font-size: 4px;
  	font-size: .25rem;
  	font-weight: 400;
  }

  .page-body-subTitle {
  	padding: 0 0 3.2px;
  	padding: 0 0 .2rem;
  	font-size: 2.88px;
  	font-size: .18rem;
  	color: #999;
  }

  .page-body-list li {
  		padding: 0 0 4.8px;
  		padding: 0 0 .3rem;
  		font-size: 2.72px;
  		font-size: .17rem;
  		line-height: 1.65;
  	}

  .page-body-list__title {
  		padding: 0 0 0.96px;
  		padding: 0 0 .06rem;
  		font-weight: 700;
  	}




