body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: #f5f5f5;
}

html {
  font-size: 5.20833vw;
}

html, body, div, p, ul, li {
  margin: 0;
  padding: 0;
}
*,
*::before,
*::after {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
ul, li {
	list-style: none;
}


